
import * as _ from '../../utils/tools'
import cookie from '../../../static/js/cookie'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

export default {
components: {
Group,
XInput
},
data: function () {
return {
username: '',
password: ''
}
},
created () {
cookie.delCookie('token')
cookie.delCookie('name')
this.$store.dispatch('setInfo')
},
methods: {
login: function () {
if (!this.username) {
this.$vux.toast.show({
text: '请输入用户名',
type: 'warn'
})
return false
}
if (!this.password) {
this.$vux.toast.show({
text: '请输入密码',
type: 'warn'
})
return false
}
let data = {
'username': this.username,
'password': this.password
}
let that = this
return this.$api.login(data).then(res => {
that.$vux.toast.text('登录成功')
let time = 3600*24*7

cookie.setCookie('name', that.username, time)
cookie.setCookie('token', res.data.token, time)
cookie.setCookie('openid', res.data.openid, time)
that.$store.dispatch('setInfo')
_.go('/center', that.$router, 2000)
}).catch(err => {
that.$vux.toast.text('用户名已存在')
return err.message
})
}
}
}



